*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  line-height: 2em;
}

:root{
  

  --light:#EAF1E4;
  --dark: #111115;
  --blue: #1B4965;
  --light-blue: #39A9DB;
  --green: #2C6E49;
  --red: #DB3A34;
  --orange: #F3A712;

  --link:var(--red);
  --link-hover:var(--orange);

  color: var(--dark);
  background-color: var(--light);

  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

body, div#root{
  flex-grow: 1;
  display: flex;
  flex-direction: column;
}
body {      
  font-family: 'Sono', sans-serif;      
}

a, a:visited{
  color: var(--link);
  text-decoration: none;
}

a:hover, a:active{
  color: var(--link-hover);
}

/* styling for active react-router NavLinks */
a.active{  
    --link:var(--light-blue);
}

code {
  font-family: 'DM Mono', source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
  color: var(--red);  
}

ul{
  list-style: none;
}



@import url('https://fonts.googleapis.com/css2?family=DM+Mono:ital,wght@0,300;0,400;0,500;1,300;1,400;1,500&family=Sono:wght@200;300;400;500;600;700;800&display=swap');


